/* eslint-disable */
import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import Layout from '@/Layout.vue';

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
   {
      path: '/',
      component: Layout,
      children: [
         { path: '/', name: 'home', component: () => import('@/views/HomePage.vue') },

         {
            path: '/courses',
            name: 'courses',
            component: () => import('@/views/Course/CourseList.vue'),
         },
         {
            path: '/courses/details/:id',
            name: 'details',
            component: () => import('@/views/Course/CourseDetails.vue'),
         },
      ],
   },

   {
      path: '/login',
      name: 'login',
      component: () => import('@/views/Account/Login.vue'),
   },
   {
      path: '/forgot',
      name: 'forgot',
      component: () => import('@/views/Account/ForgotPass.vue'),
   },
   {
      path: '/register',
      name: 'register',
      component: () => import('@/views/Account/Register.vue'),
   },
   {
      path: '/reset',
      name: 'reset',
      component: () => import('@/views/Account/ResetPass.vue'),
   },

   {
      path: '/chat',
      name: 'chat',
      component: () => import('@/views/ChatBox.vue'),
   },
   {
      path: '/setting',
      name: 'setting',
      component: () => import('@/views/Account/Setting.vue'),
   },
];

const router = new VueRouter({
   routes,
   mode: 'history',
   base: process.env.BASE_URL,
});

export default router;
